<template>
  <div>
    <el-form
      ref="form"
      :model="form"
      style="text-align:left"
      label-width="290px"
      v-loading="loading"
    >
      <el-form-item label="地址">
        乳山市<el-input style="width:162px" v-model="form.address1"></el-input>
        乡镇<el-input style="width:163px" v-model="form.address2"></el-input
        >村（居委会）
      </el-form-item>
      <el-form-item label="填表人">
        <el-input style="width:400px" v-model="form.inputUser"></el-input>
      </el-form-item>
      <el-form-item label="填表日期">
        <el-date-picker
          v-model="form.inputDate"
          style="width:400px"
          align="right"
          type="date"
          placeholder="选择日期"
        >
        </el-date-picker>
      </el-form-item>
      <el-form-item label="指示者编号">
        <el-input style="width:400px" v-model="form.pointManNo"></el-input>
        （4位，直接从打印名单表中誊抄）
      </el-form-item>
      <el-form-item label="姓名">
        <el-input style="width:400px" v-model="form.name"></el-input>
      </el-form-item>
      <el-form-item label="性别">
        <el-radio-group v-model="form.sex">
          <el-radio label="男"></el-radio>
          <el-radio label="女"></el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item label="目前状况">
        <el-radio-group v-model="form.healthy">
          <el-radio label="健在"></el-radio>
          <el-radio label="去世"></el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item label="是否从本村迁出">
        <el-radio-group v-model="form.inCurrentCountry">
          <el-radio label="是"></el-radio>
          <el-radio label="否"></el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item label="出生日期">
        <el-date-picker
          v-model="form.birthDate"
          style="width:400px"
          align="right"
          type="date"
          placeholder="选择日期"
        >
        </el-date-picker>
      </el-form-item>
      <el-form-item label="现住址">
        <el-input style="width:400px" v-model="form.currentAddr"></el-input>
      </el-form-item>
      <el-form-item label="身份证号">
        <el-input style="width:400px" v-model="form.idCardNo"></el-input>
      </el-form-item>
      <el-form-item label="手机">
        <el-input style="width:400px" v-model="form.mobilePhone"></el-input>
      </el-form-item>
      <el-form-item label="该指示者与本村其他指示者是否有亲缘关联">
        <el-radio-group v-model="form.isOtherPointmanRel">
          <el-radio label="是"></el-radio>
          <el-radio label="否"></el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item label="关联人" v-if="form.isOtherPointmanRel=='是'">
        <el-input
          style="width:400px"
          v-model="form.otherPointmanName"
        ></el-input>
      </el-form-item>
      <el-form-item label="与关联人关系" v-if="form.isOtherPointmanRel=='是'">
        <el-select
          style="width:400px;"
          v-model="form.otherPointmanRel"
          placeholder="请选择"
        >
          <el-option
            v-for="item in relations"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          >
          </el-option>
        </el-select>
        <el-input
          v-if="form.otherPointmanRel == '09'"
          style="width:200px"
          v-model="form.otherPointmanRelOther"
        ></el-input
        ><span v-if="form.otherPointmanRel == '09'"
          >2012年后共同居住1年以上者，请注明关系：（比如：公婆， 妯娌等）</span
        >
      </el-form-item>
      <el-form-item label="目前本村居住">
        <el-button size="small" type="success" @click="addIn">添加</el-button>
        <el-table :data="inKinShips" stripe style="width: 100%">
          <el-table-column prop="kinshipCode" label="亲缘关系" width="180">
            <template slot-scope="scope">
              <el-select
                style="width:100%;"
                v-model="scope.row.kinshipCode"
                placeholder="请选择"
              >
                <el-option
                  v-for="item in relations"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
                >
                </el-option> </el-select
              ><el-input
                v-if="scope.row.kinshipCode == '09'"
                style="width:400px"
                v-model="scope.row.kinshipCodeOther"
              ></el-input>
            </template>
          </el-table-column>
          <el-table-column prop="name" label="姓名">
            <template slot-scope="scope">
              <el-input v-model="scope.row.name" style="width:100%;"></el-input>
            </template>
          </el-table-column>
          <el-table-column prop="sex" label="性别" width="100">
            <template slot-scope="scope">
              <el-select v-model="scope.row.sex" style="width:100%;">
                <el-option value="男" label="男"></el-option>
                <el-option value="女" label="女"></el-option>
              </el-select>
            </template>
          </el-table-column>
          <el-table-column prop="birthdate" label="出生日期">
            <template slot-scope="scope">
              <el-date-picker
                style="width:100%;"
                v-model="scope.row.birthdate"
                align="right"
                type="date"
                placeholder="选择日期"
              >
              </el-date-picker>
            </template>
          </el-table-column>
          <el-table-column prop="idcardNo" label="身份证号">
            <template slot-scope="scope">
              <el-input
                v-model="scope.row.idcardNo"
                style="width:100%;"
              ></el-input>
            </template>
          </el-table-column>
          <el-table-column prop="remark" label="备注">
            <template slot-scope="scope">
              <el-input
                type="textarea"
                style="width:100%;"
                v-model="scope.row.remark"
              ></el-input>
            </template>
          </el-table-column>
          <el-table-column prop="remark" label="操作" width="80">
            <template slot-scope="scope">
              <el-button
                type="danger"
                icon="el-icon-delete"
                @click="delInKinShips(scope.$index)"
                circle
              ></el-button>
            </template>
          </el-table-column>
        </el-table>
      </el-form-item>
      <el-form-item label="目前外村居住">
        <el-button size="small" type="success" @click="addOut">添加</el-button>
        <el-table :data="outKinShips" stripe style="width: 100%">
          <el-table-column prop="kinshipCode" label="亲缘关系" width="180">
            <template slot-scope="scope">
              <el-select
                style="width:100%;"
                v-model="scope.row.kinshipCode"
                placeholder="请选择"
              >
                <el-option
                  v-for="item in relations"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
                >
                </el-option> </el-select
              ><el-input
                v-if="scope.row.kinshipCode == '09'"
                style="width:400px"
                v-model="scope.row.kinshipCodeOther"
              ></el-input>
            </template>
          </el-table-column>
          <el-table-column prop="name" label="姓名">
            <template slot-scope="scope">
              <el-input v-model="scope.row.name" style="width:100%;"></el-input>
            </template>
          </el-table-column>
          <el-table-column prop="sex" label="性别" width="100">
            <template slot-scope="scope">
              <el-select v-model="scope.row.sex" style="width:100%;">
                <el-option value="男" label="男"></el-option>
                <el-option value="女" label="女"></el-option>
              </el-select>
            </template>
          </el-table-column>
          <el-table-column prop="birthdate" label="出生日期">
            <template slot-scope="scope">
              <el-date-picker
                style="width:100%;"
                v-model="scope.row.birthdate"
                align="right"
                type="date"
                placeholder="选择日期"
              >
              </el-date-picker>
            </template>
          </el-table-column>
          <el-table-column prop="idcardNo" label="身份证号">
            <template slot-scope="scope">
              <el-input
                v-model="scope.row.idcardNo"
                style="width:100%;"
              ></el-input>
            </template>
          </el-table-column>
          <el-table-column prop="address" label="现住址">
            <template slot-scope="scope">
              <el-input
                v-model="scope.row.address"
                style="width:100%;"
              ></el-input>
            </template>
          </el-table-column>
          <el-table-column prop="remark" label="备注">
            <template slot-scope="scope">
              <el-input
                type="textarea"
                style="width:100%;"
                v-model="scope.row.remark"
              ></el-input>
            </template>
          </el-table-column>
          <el-table-column prop="remark" label="操作" width="80">
            <template slot-scope="scope">
              <el-button
                type="danger"
                icon="el-icon-delete"
                @click="delOutKinShips(scope.$index)"
                circle
              ></el-button>
            </template>
          </el-table-column>
        </el-table>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" :disabled="loading" @click="confirm">提交</el-button>
        <el-button :disabled="loading">取消</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
export default {
  data() {
    return {
      loading:false,
      form: {
        address1: "",
        address2: "",
        inputUser: "",
        inputDate: new Date(),
        pointManNo: "",
        name: "",
        sex: "",
        healthy: "",
        inCurrentCountry: "",
        birthDate: "",
        currentAddr: "",
        idCardNo: "",
        mobilePhone: "",
        isOtherPointmanRel: "否",
        otherPointmanName: "",
        otherPointmanRel: ""
      },
      relations: [
        { label: "父亲", value: "01" },
        { label: "母亲", value: "02" },
        { label: "夫妻", value: "03" },
        { label: "未婚子女", value: "04" },
        { label: "已婚子女", value: "05" },
        { label: "未婚孙儿(女)/外孙儿(女)", value: "06" },
        { label: "已婚孙儿(女)/外孙儿(女)", value: "07" },
        { label: "兄弟姐妹", value: "08" },
        {
          label: "其他",
          value: "09"
        }
      ],
      inKinShips: [],
      outKinShips: []
    };
  },
  methods: {
    confirm() {
      this.loading = true;
      this.axios({
        method: "post",
        url: "http://localhost:8050/qs/api/v1/addpm",
        data: JSON.stringify({
          //这里是发送给后台的数据
        })
      })
        .then(response => {
          this.$message.success("保存成功！");          
          this.loading = false;
          console.log(response); //请求成功返回的数据
        })
        .catch(error => {
          this.loading = false;
          this.$message.error(error)
          console.log(error); //请求失败返回的数据
        });
    },
    addIn() {
      this.inKinShips.push({
        kinshipCode: "",
        name: "",
        sex: "",
        birthdate: "",
        idcardNo: "",
        remark: ""
      });
    },
    addOut() {
      this.outKinShips.push({
        kinshipCode: "",
        name: "",
        sex: "",
        birthdate: "",
        idcardNo: "",
        remark: ""
      });
    },
    delInKinShips(index) {
      this.inKinShips.splice(index, 1);
    },
    delOutKinShips(index) {
      this.inKinShips.splice(index, 1);
    },
  }
};
</script>
<style scoped></style>



