<template>
  <div>
    <div class="operButton">
      <el-button size="small" type="success" @click="add">添加</el-button>
      <el-button size="small" type="success">删除</el-button>
      <el-button size="small" type="success">导出</el-button>
    </div>
    <el-table :data="tableData" stripe style="width: 100%">
      <el-table-column prop="date" label="日期" width="180"> </el-table-column>
      <el-table-column prop="name" label="姓名" width="180"> </el-table-column>
      <el-table-column prop="address" label="地址"> </el-table-column>
    </el-table>
    <add-win v-if="addFlag"></add-win>
  </div>
</template>
<script>
import AddWin from "./Add.vue";
export default {
  components: {
    AddWin
  },
  data() {
    return {
      addFlag: false,
      tableData: [
        {
          date: "2016-05-02",
          name: "王小虎",
          address: "上海市普陀区金沙江路 1518 弄"
        },
        {
          date: "2016-05-04",
          name: "王小虎",
          address: "上海市普陀区金沙江路 1517 弄"
        },
        {
          date: "2016-05-01",
          name: "王小虎",
          address: "上海市普陀区金沙江路 1519 弄"
        },
        {
          date: "2016-05-03",
          name: "王小虎",
          address: "上海市普陀区金沙江路 1516 弄"
        }
      ]
    };
  },
  methods: {
    add() {
      // this.addFlag = true;
      this.$router.push({ path: "/Add" });
    }
  }
};
</script>
<style scoped>
.operButton {
  text-align: left;
  padding-left: 10px;
}
</style>



